.form {
  .input {
    margin-bottom: 10px;
    &__search {
      width: 250px;
      padding: 5px;
      border: solid 1px greenyellow;
      background-color: transparent;
      text-align: center;

      &:hover {
        &::placeholder {
          color: transparent;
        }
      }
      &:focus {
        border: solid 2px aquamarine;
        &::placeholder {
          color: transparent;
        }
      }
      &::placeholder {
        color: rgb(43, 183, 226);
      }
    }
    .removeBtn {
      padding: 5px;
      border: solid 1px aquamarine;
      border-left: transparent;
      background-color: aquamarine;
      color: black;
      &:hover {
        cursor: pointer;
      }
    }
    &__checkbox {
      display: none;
      &--label {
        color: rgb(43, 183, 226);
        padding-left: 25px;
        &:hover {
          color: greenyellow;
          cursor: pointer;
        }

        .alien {
          &:hover {
            font-family: Alien_S;
            color: aquamarine;
          }
        }
      }
    }
  }
  .checkbox {
    .icon {
      position: absolute;
      z-index: -1;
    }
    .active {
      color: aquamarine;
    }
  }
}
